.accordion {
    max-width: 592px;
    margin: 0 auto;

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        border: 1px solid #eaeffc;
        padding: $grid-unit*3 $grid-unit*4;
        border-radius: 8px;
        margin-bottom: $grid-unit;

        @media (max-width: 768px) {
            padding: $grid-unit*2 $grid-unit*2;
        }
    }

    &__item-heading {
        @include noSelect;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-family: $font-secondary;
        font-weight: 600;
        font-size: 16px;
        line-height: $grid-unit*3;
        letter-spacing: 0.0016px;
        margin: $grid-unit*-3 $grid-unit*-4;
        padding: $grid-unit*3 $grid-unit*4;
        cursor: pointer;

        @media (max-width: 768px) {
            margin: $grid-unit*-2 $grid-unit*-2;
            padding: $grid-unit*2 $grid-unit*2;
        }
    }

    &__item-heading-icon {
        transition: $transition;

        svg.icon {
            width: 16px;
            height: 10px;
            fill: $color-royalBlue;
        }
    }

    &__body {
        max-height: 0;
        overflow: hidden;
        transition: all 0.3s ease-in-out;
    }

    &__item--active {

        .accordion__body {
            max-height: 500px;
            padding-top: $grid-unit*2;
        }
        .accordion__item-heading-icon {
            transform: rotate(180deg);
        }
    }

    &--wrapper {
        @include container-md;
        margin-bottom: $grid-unit*10;
    }


    &-container {
        h2 {
            text-align: center;
            margin-bottom: $grid-unit*5;
        }
    }
}

