.burger-menu {
    margin-left: 20px;

    &__button {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        position: relative;
        width: $grid-unit*4;
        height: $grid-unit*4;
        border: 0;
        padding: 0;
        outline: 0;
        background: 0 0;
        line-height: 1;
        cursor: pointer;

        span {
            flex-grow: 1;
            position: relative;
            width: 100%;
            height: 2px;
            border: none;
            transition: all .35s ease;
            background: $color-black;
            cursor: pointer;
            margin-bottom: $grid-unit;
            transition: $transition;

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
}

.header__mobile-open {
    .burger-menu__button {

        span:nth-of-type(1) {
            top: 10px;
            transform: rotate(45deg);
        }

        span:nth-of-type(2) {
            opacity: 0;
        }

        span:nth-of-type(3) {
            top: -10px;
            transform: rotate(-45deg);
        }
    }
}