.modal {
    display: none;
    position: relative;
    z-index: 9998;

    &__open {
        display: block;
    }

    &__close-btn {
        display: block;
        position: absolute;
        top: $grid-unit*3;
        right: $grid-unit*3;
        width: $grid-unit*2;
        height: $grid-unit*2;

        @media (max-width:768px) {
            top: 10px;
            right: 12px;
        }
    }


    &__body {
        position: relative;
        z-index: 9998;
        width: 100%;
        max-width: 544px;
        margin: 0 auto;
        padding: $grid-unit*10;
        transition: all 0.3s ease-in;
        background: #ffffff;
        box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
        border-radius: 16px;

        @media (max-width:768px) {
            max-width: 288px;
            padding: 16px;
            padding-top: 32px;
        }
    }

    &__title {
        text-align: center;
        margin-bottom: $grid-unit*5;

        @media (max-width:768px) {
            margin-bottom: 8px;
        }
    }

    &__form {

        input {
            width: 100%;
        }
    }

    &__button-submit {
        display: block;
        margin-top: $grid-unit*4;
        margin-left: auto;
        margin-right: auto;

        @media (max-width:768px) {
            margin-top: $grid-unit*2;
        }
    }

    &__form-confirmation {
        font-size: 14px;
        line-height: 19px;
    }

    &__form-confirmation-link {
        color: $color-royalBlue;

        &:hover, &:focus {
            color: $color-astronaut;
        }
    }

    &--wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: fixed;
        z-index: 9999;
        top: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: opacity 0.3s ease;
        background-color: rgba(35, 36, 115, 0.5);

        @media (max-width:768px) {
            justify-content: flex-start;
            overflow: scroll;
            padding: 16px 0;
        }
    }
}

