.map {
    position: relative;
    width: 100%;
    height: 456px;

    @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
    }

    #map {
        width: 100%;
        height: 100%;

        @media (max-width: 768px) {
            order: 2;
            width: 100%;
            height: 200px;
        }
    }

    &__info {
        position: absolute;
        top: $grid-unit*10;
        left: calc((100vw - 1248px)/2);
        max-width: 488px;
        padding: $grid-unit*5 $grid-unit*9;
        background: #ffffff;
        border-radius: 8px;

        @media (max-width: 768px) {
            order: 1;
            position: relative;
            top: auto;
            left: auto;
            max-width: 100%;
            padding: 32px 16px 40px 16px;
            border-radius: 0;
        }
    }

    &__info-title {
        margin-bottom: $grid-unit*2;
    }

    &__info-address {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 12px;
    }

    &__info-tel {
        display: flex;
        justify-content: flex-start;
        margin-bottom: $grid-unit*3;
        font-size: 16px;
        line-height: 26px;
    }

    &__info-tel-link {
        color: $color-royalBlue;

        &:hover, &:focus {
            color: $color-astronaut;
        }
    }
}